import '../nouislider/nouislider.min.css';
import '../nouislider/nouislider.min.js';
import noUiSlider from 'nouislider';

export default class AppView {
   
    constructor(state) {
        this.state = state;
    }

    render() {
        const header = document.createElement('h1');
header.classList.add('header');
header.innerText = 'Dogs';
document.body.append(header);

const dogsList = document.createElement('div');

dogsList.classList.add('dogs-list');
document.body.append(dogsList);


function createCard(src, name) {
    const div = document.createElement('div');
    div.classList.add('div-img');
    const img = document.createElement('img');
    img.classList.add('dog');
    div.append(img);
    dogsList.append(div);
    img.src = src;
    const p = document.createElement('p');
    p.classList.add('p');
    p.innerText = name;
    div.append(p);
}

for(let i = 0; i < 20; i++) {
    const d = this.state;
    createCard(d[i].url, d[i].name)
}

//  const sliders = document.querySelectorAll('input[type="range"]');

const p0 = document.createElement('p');
p0.id = p0;
document.body.append(p0);

const rangeSlider = document.createElement('div');
rangeSlider.id = "range";
document.body.append(rangeSlider);

const p1 = document.createElement('p');
p1.id = 'p1';
document.body.append(p1);

if(rangeSlider) {
    noUiSlider.create(rangeSlider, {
        start: [1950, 2010],
        connect: true,
        step: 10,
        range: {
            'min': 1950,
            'max': 2010
        }
    });
}

const pp = [p0, p1];

rangeSlider.noUiSlider.on('update', function(values, handle) {
    pp[handle].innerText = Math.round(values[handle])
})

    }
}